import React from 'react';
import { Link } from 'react-router-dom'; // Use Link for routing

function Navbar() {
  return (
    <nav style={styles.navbar}>
      <div style={styles.logo}>Club Events</div>
      <ul style={styles.navLinks}>
        <li style={styles.navItem}>
          <Link to="/" style={styles.navLink}>Home</Link> {/* Use Link for Home */}
        </li>
        <li style={styles.navItem}>
          <Link to="/events" style={styles.navLink}>Events</Link> {/* Use Link for Events */}
        </li>
        <li style={styles.navItem}>
          <Link to="/about" style={styles.navLink}>About</Link> {/* Use Link for About */}
        </li>
        <li style={styles.navItem}>
          <Link to="/contact" style={styles.navLink}>Contact</Link> {/* Use Link for Contact */}
        </li>
      </ul>
    </nav>
  );
}

const styles = {
  navbar: {
    backgroundColor: '#333',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: '10px 20px',
    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
  },
  logo: {
    fontSize: '1.8rem',
    fontWeight: 'bold',
    color: 'white',
    textTransform: 'uppercase',
  },
  navLinks: {
    listStyle: 'none',
    display: 'flex',
    margin: 0,
    padding: 0,
  },
  navItem: {
    marginLeft: '20px',
  },
  navLink: {
    color: 'white',
    textDecoration: 'none',
    fontSize: '1rem',
    fontWeight: '600',
    padding: '10px 15px',
    borderRadius: '5px',
    transition: 'background-color 0.3s ease',
  },
};

export default Navbar;
